body {
 background-color: black;

}
section.loader {
 /* border: yellow solid; */
 overflow: hidden; 
 display: flex;
align-items: center;
justify-content: center;
height: 300px;
width: 150px;
border-radius: 6px;
margin:5px  auto;
}

.loader div{
 width: 15px;
 height: 15px;
 /* background-color: aqua; */
 background-color: var(--sec-color);
 border-radius: 50%;
 margin-right: 3px;
}
.loader div:nth-child(4){
 animation: infinite alternate anime1 ease-in 1s;
}
.loader div:nth-child(2){
 animation: infinite alternate anime2 1s 1s;
}
.loader div:nth-child(3) {
 animation: infinite alternate anime3 1s 1s;

}
@keyframes anime1 {
 10% {
  transform: translateX(40px);
 }
 20% {
  /* transform: translateX(50px); */
 }
}
@keyframes anime2 {
 10% {
  transform: translateX(-40px);
 }
 /* 20% {
 } */
}
@keyframes anime3 {
 10% {
 }
 20% {
  transform: translateY(-40px);
 }
}
