html {
  box-sizing: border-box !important;
  font-size: 1em;
  --bg-color: #111;
  --sec-color: rgb(10, 236, 10);
  --box-shadow: 2px 2px 15px rgba(10, 236, 10, 0.5) ;
  --hov-box-shadow: 6px 6px 80px rgba(10, 236, 10, 0.5) ;
  --hov-input-box-shadow: 1px 1px 20px rgba(10, 236, 10, 0.5) ;
  --white-color: #fff;
/* --transition: all ease-in-out 300ms; */
--transition: all 300ms cubic-bezier(0.215, 0.610, 0.355, 1);
--font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
sans-serif;
}
button{
  transition: var(--transition);
   margin-left: 10px;
    border-radius: 6px;
    background: var(--sec-color);
    height: 3rem;
      height: 2rem;
      color: var(--white-color);
      box-shadow: var(--box-shadow);
}
* {
  margin: 0;
  box-sizing: border-box !important;
}
body {
  background-color: var(--bg-color);
  box-sizing: border-box;
  margin: 0;
  font-family:var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  width: 3px;
  background: blueviolet;
}
body::-webkit-scrollbar:hover {
  /* width: auto; */
  background: aquamarine;
}
.active-div  {
  height: 5px;
  border-radius: 6px;
  margin-top: 5px;
  background-color: var(--sec-color);
  transition: var(--transition);
width:0 ;
  position: absolute;
  top: 100%;
}
 li.active-anchor .active-div , li:hover > .active-div{
  width: 100%;
  transition: var(--transition);
}
.dn{
  transition: cubic-bezier();
}
.youtube {
position: absolute;
display: flex;
justify-content: center;
align-items: center;
top: 0px;
right: 10px;
left: 10px;
bottom: 5px;
transition: var(--transition);
border-radius: 6px;
background-color: rgba(10, 236, 10, 0.1) ;
/* z-index: -55; */
z-index: -55;
}
#img-cont {
  position: relative;
}
#img-cont:hover  .youtube {
  z-index: 55;
  transition: var(--transition);
}