/* THIS STYLES THE HEADER COMPONENT */
.button-wrapper {
display: flex;
justify-content: space-between;
width: 17rem;
/* margin-left: 50px; */
margin-top: 2rem;
animation: 1.9s  1 show;
/* border: solid yellow; */
}
button {
  margin-left: -1px;
 background-color: rgba(11, 11, 11,0.7 );
 font-size: 1rem;
  /* transition: var(--transition); */
    border-radius: 6px;
    background: var(--sec-color);
      color: var(--white-color);
      box-shadow: var(--box-shadow);
      font-family: var(--font-family);
outline: none;
border: none;
 text-transform: capitalize;
width: 6rem;
font-weight: 900;
display: block;
transition: 200ms all cubic-bezier(0.175, 0.885, 0.32, 1.275) ;
padding:  0.5rem;
height: auto;
text-align: center;
}
button:not(:nth-last-child(2)){
 color: var(--sec-color);
 background-color: var(--white-color);
 border: solid 1px var(--sec-color);
}
button:hover{
   box-shadow: var(--hov-box-shadow);
}
.overview {
 width: 30rem;
 animation: 1.5s  1 show ;
}
.title {
 animation: 1s 1 show;

}
@keyframes show {
  0%{
transform: translateY(-1000px);
  }   
}